
@import './../../libs/utils.scss';

.button {
  background-color: $main;
  color: white;
  padding: 10px 30px;
  border-radius: 7px;
  border: 0;
  transition: all 0.4s ease;
  font-size: 1em;
  width: 100%;
  cursor: pointer;

  &:hover {
    background-color: lighten($main, 5%);
    box-shadow: 0px 0px 10px lighten($main, 20%);
  }
}